export default {
	props: {
		num: Number,
		numList: {
			type: Array,
			default: () => [
				{
					name: '开始'
				},
				{
					name: '等待'
				},
				{
					name: '错误'
				},
				{
					name: '完成'
				}
			]
		}
	},

	methods: {
		numSteps() {
			const len = this.numList.length;

			this.num = len - 1 ? 0 : this.num + 1;
		}
	}
};
